@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@500&display=swap);
body{padding:0;margin:0}#app-top{background-color:rgba(202,202,202,0.877);width:100%;height:100vh}#tweet{display:flex;flex-wrap:wrap;width:500px;max-height:300px;border:3px solid lightgreen;border-radius:10px;background-color:lightblue}#tweet #tweet-name{margin-left:20px}#tweet #tweet-text{direction:rtl;padding-right:20px;font-size:large;width:90%;align-self:flex-end}#tweet #tweet-like-button{margin-left:20px;background-color:lightskyblue;border:2px solid lightseagreen;border-radius:10px;width:50px;height:30px}#tweet #tweet-like-count{font-size:x-large;margin-top:2px;margin-left:6px;width:10%}#tweet #tweet-like-button:hover{background-color:lightseagreen;margin-left:18px;width:54px;height:35px}#navbar-top{background-color:rgba(46,47,54,0.5);width:100%;height:15vh;padding-top:0px;position:fixed;display:flex}#navbar-top #logo{margin-right:auto;align-self:center;font-family:'Mukta', sans-serif;font-size:7.5vh;margin-right:auto;margin-left:2vw;color:#ffd900}#navbar-top #NavItem{text-decoration:none;display:flex;align-items:center;margin:1.875vh}#navbar-top #NavItem img{width:7.5vh;height:7.5vh;transition:all 300ms}#navbar-top #NavItem img:hover{-webkit-transform:scale(1.25);transform:scale(1.25)}#navbar-top #NavItem p{font-size:3.75vh;width:15vh;padding:10px;text-align:center;color:#ffd5d5;background:rgba(0,0,0,0.2);border-radius:10px;transition:all 300ms}#navbar-top #NavItem p:hover{background:rgba(41,40,40,0.2);-webkit-transform:scale(1.25);transform:scale(1.25);border-radius:20px}#navbar-top #DropdownMenu{position:fixed;top:15vh;right:10px;background-color:orchid}#home_page-top{background-color:orangered;padding-top:15vh;width:300px;height:80vh}#about_page-top{background-color:orangered;padding-top:15vh;width:300px;height:300px}

